.User {
	background-color: white;
	margin: 100px auto 0 auto;
	border: 1px solid #dedede;
	width: 500px;
	height: 500px;
	color: #27a645;
	-webkit-box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	-moz-box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	box-shadow: 0px 10px 28px 0px rgba(0, 0, 0, 0.75);
	h3 {
		text-align: center;
		font-size: 1.5em;
		margin: 50px 0 55px 0;
	}
	.Wrapper {
		display: flex;
		justify-content: space-between;
		.Left {
			margin-left: 30px;
		}
		.Right {
			margin-right: 30px;
		}
	}
	a {
		color: #27a645;
	}
	.Label {
		margin-left: 20px;
	}
	.Button {
		text-align: center;
		display: flex;
		justify-content: center;
		button {
			width: 150px;
			&:hover {
				background-color: #27a645;
			}
			&:active {
				background-color: darken(#27a645, 10%);
			}
		}
	}
	a {
		margin-top: 10px;
		margin-right: 30px;
		float: right;
		transition-duration: .15s;
		&:hover {
			cursor: pointer;
			color: darken(#27a645, 10%);
		}
	}
}
